.Left{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 3rem;
    background-color: var(--primary-cyan);
    border-radius: 0 0 0 1rem;
}

.Left > *{
    width: 90%;
}

.Left h3{
    color: var(--white);
}

.Left span{
    display: flex;
    width: 100%;
    gap:1rem;
    align-items: center;
    color: var( --neutral-lgray);
}


.Left span h1{
    color: var(--white);
    font-weight: var(--font-weight700);
}

.Left p{
    color: var(--neutral-lgray);
}

.Left button{
    padding: 1rem 0;
    outline:0;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 0 1px 0;
    background-color: var(--primary-byellow);
    color: var(--white);
    font-weight: var(--font-weight700);
}

@media screen and (max-width:375px) {
    .Left{
        border-radius: 0;
    }
}