@import "https://fonts.googleapis.com/css?family=Karla";

:root{
    --primary-cyan:#2ab2af;
    --primary-cyan1:#2ab2b0bf;
    --primary-byellow:#c0df34;
    --neutral-lgray:#e5eff5;
    --neutral-gblue:#98a6bd;
    --white:#ffff;
    --font-size:16px;
    --font-weight400:500;
    --font-weight700:700;
}
*{
    font-family: 'Karla';
}

body{
   
    width: 100%;
    background-color: var(--neutral-lgray);
}
h1,
h2,
h3,
h4,
h5,
h6,
body,
html,
p,
span,
b,
u,
i{
    margin:0;
    padding:0;
}

main{
    width:50%;
    border-radius: 1rem;
    background-color: var(--white);
    margin: 3rem auto;
    box-shadow: 0 0 1px 0;
}

footer{
    display:hidden;
}

@media screen and (max-width:375px) {
    main{
        width: 85%;
    }
}