.Head{
    padding:3em;
}

.Head{
    display: grid;
    grid-template-columns: 100%;
    gap:1rem;
}

.Head > *{
    width: 90%;
}
.Bottom{
    display: grid;
    grid-template-columns: 50% 50%;
} 

@media screen and (max-width:375px) {
    .Head{
        padding: 1rem;
    }
    .Bottom{
        grid-template-columns: 100%;
    }
}
