.Right{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 3rem;
    background-color: var(--primary-cyan1);
    border-radius: 0 0 1rem 0;
}

.Right h3{
    color: var(--white);
}

.Right p{
    width: 80%;
    text-align: left;
    color: var(--neutral-lgray);
}

@media screen and (max-width:375px) {
    .Right{
        border-radius: 0 0 1rem 1rem;
    }    
}